import React from 'react';
import {Card, Image} from 'react-bootstrap';
import cx from 'classnames';
import styles from '../assets/styles/MenuBrand.module.css';
function MenuBrand(props) {
  const { logo, background, text, selected, category } = props;
  return (
    <div className="mb-2">
      <Card className={cx(styles.brand_card)}>
        <img src={background} className="w-100 h-100" style={{objectFit:'cover'}}/>
      </Card>
      <div className='mt-2'>
        <span className='badge text-primary shadow-sm' style={{background: 'var(--tp-primary-4)'}}>{category}</span>
        <div className={cx('small fw-bold text-truncate font-tp mt-1')}>{text}</div> 
      </div>
    </div>
  );
}

export default MenuBrand;
