/**
 * Menu List component for the customer
 **/
import { useEffect, useState } from 'react';
import { Container, Row, Card, Col, Image, Button } from 'react-bootstrap';
import MenuCard from './MenuCard';
import styles from '../assets/styles/MenuCardList.module.css';
import { useHistory } from 'react-router';
import { renderFromText } from './Helper';
export default function MenuCardList(props) {
  // history
  const {menu, orders, table, categories} = props;
  const history = useHistory();
  const [filteredMenu, setFilteredMenu] = useState([]);
  const clickViewItem = (id) => {
    if (id) {
      // push route to the product detail page
      history.push(`/customer/table-ordering/${props.table}/menu/${id}`);
    }
  };
  // if there is an order already, return the object
  const getOrder = (id) => {
    if(! props.orders?.id){
      return {}
    }
    const orderItems = props.orders?.order_items;
    if(orderItems.length){
      const order = orderItems.filter((x) => x.menu_item_id == id)
      if (order.length) {
        return order;
      }
    }
    return {};
  };
  const renderMenuList = (category_id) => {
    if (!props.menu.length) {
      return null;
    }
    const filtered_menu = props.menu.filter(
      (x) => x.category.id == category_id
    );

    if (filtered_menu.length) {
      // sort by availability 
      const sorted_menu = filtered_menu.sort((a,b) => b.available - a.available)

   
      return sorted_menu.map((item) => {
        return (
          <Col lg='4' xs='12' md='6' className={styles.col} key={item.id}>
            <MenuCard
              name={item.name}
              image={item.image?.public_url}
              id={item.id}
              description={item.description}
              price={item.price}
              clickViewItem={clickViewItem}
              available={item.available}
              selected={getOrder(item.id)}
              truncateCount={50}
              addFromText={renderFromText(item)}
            />
          </Col>
        );
      });
    }
    return null;
  };

  const handleFilteredMenu = () => {
    if(menu.length && categories.length){
      let array = [];
      categories.forEach((item) => {
        let object = {};
        const category_menu = menu.filter((i) => i.category?.id === item.id);
        if(category_menu.length){
          const sorted_category_menu = category_menu.sort((a,b) => b.available - a.available);
          object = {
            ...item,
            items: sorted_category_menu
          };
          array.push(object);
        }
      })
      setFilteredMenu(array);
    }
  };

  useEffect(() => {
    handleFilteredMenu();
  }, [menu]);

  return (
    <Container fluid className={`${styles.container} position-relative`}>
      {
        filteredMenu.length ? 
        <div>
          {filteredMenu.map((item,index) => {
            return (
              <div id={`category-header-${item.id}`} key={index} className="bg-white">
                <Container className="py-2 mt-3" fluid>
                  <h5 className="fw-bold">
                    {item.label}
                  </h5>
                </Container>
                <Row className={styles.row}>
                  {item.items.map((i,ind) => {
                    return (
                      <Col lg='4' xs='12' md='6' className={styles.col} key={i.id}>
                        <MenuCard
                          name={i.name}
                          image={i.image?.public_url}
                          id={i.id}
                          description={i.description}
                          price={i.price}
                          clickViewItem={clickViewItem}
                          available={i.available}
                          selected={getOrder(i.id)}
                          truncateCount={50}
                          addFromText={renderFromText(i)}
                        />
                      </Col>
                    )
                  })}
                </Row>
              </div>
            )
          })}
        </div>
        :
        null
      }
      {/* {props.categories.length ? (
        <div>
          {props.categories.map((item, index) => {
            return (
              <div id={`category-header-${item.id}`} key={index} className="bg-white">
                <Container className='py-1' fluid>
                  <div className='fs-5 fw-bold'>
                    {item.label}
                  </div>
                </Container>
                <Row className={`${styles.row}`}>{renderMenuList(item.id)}</Row>
              </div>
            );
          })}
        </div>
      ) : null} */}
    </Container>
  );
}
