/**
 * CUSTOMER NAVBAR WHEN VIEWING AUTH
 * This is a component for the customer view*/

import React, { useState } from 'react';
import { Container, Col, Row, Form, Navbar, Image } from 'react-bootstrap';
import { useHistory, useParams } from 'react-router';
import { ReactComponent as TableSvg } from '../assets/icons/table.svg';
import styles from '../assets/styles/Navbar.module.css';
import { Button } from 'react-bootstrap';
import { useCustContext } from '../CustomerContext';
import LetteredAvatar from 'react-lettered-avatar';
import cx from 'classnames';
import { BsChevronLeft } from 'react-icons/bs';
export default function NavbarCust(props) {
  const history = useHistory();
  const {table_id} = useParams();
  const { getTableNumber, tableInfo, user } = useCustContext();
  return (
    <Navbar expand='lg' className={cx((props.className),{
      'bg-white sticky-top' : ! props.className,
    })}>
      <Container
        fluid
        className='d-flex justify-content-between align-items-center'
      >
        {props.page == 'home' && (
          <>
            {/* we are showing the current table no and current group */}
            {/* <div
              className='d-flex align-items-center tp-rounded px-3 py-1'
              style={{
                background: 'var(--tp-primary-4)',
              }}
              onClick={() => {history.push(`/customer/table-ordering/${table_id}/brands`)}}
            >
              <div>
                <TableSvg />
              </div>
              <div className='ms-1 fw-bold'>{getTableNumber()}</div>
            </div> */}
            <div
              onClick={() => history.push(`/customer/table-ordering/${table_id}/brands`)}
            >
              <span className="d-flex align-items-center fw-bold lh-sm  cursor-pointer" >
                <BsChevronLeft className='me-2 stroke-width-2' />
                <div>
                  <div className='small truncate fw-normal'>{tableInfo.location?.name}</div>
                  <div className='text-primary'>{getTableNumber()}</div>
                </div>
              </span>
            </div>
            <div className='text-truncate d-flex align-items-center'>
              {/*<div className={styles.avatar}></div>*/}
              <div className='me-2 text-end lh-sm'>
                <div className='small' style={{color: 'var(--bs-gray-600)'}}>Guest User</div>
                <div className='fw-bold'>{user?.guess_name}</div>
              </div>
              <LetteredAvatar
                name={user?.guess_name}
                color='var(--tp-primary)'
                backgroundColor='var(--tp-primary-4)'
              />
            </div>
          </>
        )}
        {/* Go back will need to be accompanied with a title  */}
        {props.goBack && (
          <>
            <div className='d-flex col-1 align-items-center text-secondary'>
              <Button
                className='rounded-pill border-0 text-secondary bg-transparent ps-0 pb-2'
                onClick={() => history.goBack()}
              >
                <BsChevronLeft size={20} style={{strokeWidth: '2px'}}/>
              </Button>
            </div>
            <div>
              {props.title && <h4 className='my-0 fw-bold'>{props.title}</h4>}
            </div>
            <div className='col-1'>&nbsp;</div>
          </>
        )}
      </Container>
    </Navbar>
  );
}
