/**
 * The item detail modifier component */

import React, { useEffect, useRef, useState } from 'react';
import { Container, ListGroup, Form } from 'react-bootstrap';
import { useParams } from 'react-router-dom';

export default function ItemDetailVariant(props) {
  const { editItemId } = props;
  // the save state here
  const [selectedVariant, setSelectedVariant] = useState({});
  const type = props.type ? props.type : 'radio';
  // list group onClick event
  // NOTE: do the option have id?
  const clickSelectVariant = (option, id) => {
    /*if (!modifier_id || !option_id) {
       return false;
     }
     // update selected modifier
     setSelectedModifier({
       ...selectedModifier,
       [modifier_id]: option_id,
     });*/

    // let copy = [...selectedVariant];
    // copy[index] = option;
    const updated_variant = {
      ...selectedVariant,
      [id] : option
    };
    setSelectedVariant(updated_variant);
    props.handleSetGroupVariant(updated_variant);
  };
  const handleSetGroupVariant = (arr) => {
    console.log(arr.join(' - '));
  };
  useEffect(() => {
    if (props.variants) {
      if (editItemId === undefined) {
        let obj = {};
        props.variants.forEach((item) => obj[item.id] = item.options[0]);
        setSelectedVariant(obj);
        props.handleSetGroupVariant(obj);
      } else {
        if (props.orderVariants?.name) {
          let arr = props.orderVariants.name.split(' - ');
          // exact comparison with the current variant item 
          if(arr.length){
            let obj = {};
            arr.forEach((item) => {
              // loop through variant to see which
              props.variants.forEach((variant) => {
                let id = variant.id;
                const option = variant.options.find((option) => option == item);
                if(option){
                  obj[id] = option;
                }
             }) 
            })
            setSelectedVariant(obj);
          }

          // setSelectedVariant(arr);
          // console.log(arr);
        }
      }
    }
  }, [props.variants, props.orderVariants]);
  // list the options

  const renderOptionList = (variant, id) => {
    if (!variant.options.length) {
      return false;
    }

    // create the modifier list
    return (
      <ListGroup variant='flush'>
        {variant.options.map((item, index) => {
          return (
            <ListGroup.Item
              key={index}
              onClick={(e) => {
                clickSelectVariant(item, id);
              }}
              className={`d-flex justify-content-between align-items-center pointer`}
            >
              <div>
                <Form>
                  <Form.Check
                    readOnly
                    type={type}
                    label={item}
                    checked={
                      selectedVariant[id] === item ? true : false
                    }
                  />
                  
                </Form>
              </div>
            </ListGroup.Item>
          );
        })}
      </ListGroup>
    );
  };
  return Array.isArray(props.variants) && props.variants.length
    ? props.variants.map((variant, variant_index) => {
        return (
          <>
            <div className='bg-gray-100 py-1' key={variant.id}>
              <Container fluid>
                <p className='mb-0 fw-bold'>{variant.name}</p>
              </Container>
            </div>
            {renderOptionList(variant, variant.id)}
          </>
        );
      })
    : null;
}
