import React, {useEffect, useState} from "react";

import { Container, Row, Col, Card, Button } from "react-bootstrap";
import CustomerLayout from "../layout/CustomerLayout";
import Search from "../components/Search";
import NavbarCust from "../components/NavbarCust";
import { useCustContext } from "../CustomerContext";
import BrandList from "../components/BrandList";
import styles from '../assets/styles/MenuCardList.module.css';
import defaultImage from '../assets/images/menu-img-placeholder.png';
import cx from 'classnames';
import { truncateText } from "../components/Helper";
import {FaStoreAlt} from 'react-icons/fa';
import { useHistory, useParams } from "react-router";
import {BsChevronLeft} from 'react-icons/bs';
import NoData from "../components/NoData";
import {setItemId} from '../CustomerContext';
export default function GlobalSearch(){
  const [keyword, setKeyword] = useState('');
  const [filteredItems, setFilteredItems] = useState([]);
  const {brands, setItemId , handleSelectBrand} = useCustContext();
  const history = useHistory();
  const {table_id} = useParams();

  const handleSearchKeyword = () => {
    if(brands && brands.length){
      // filter only available stores 
      let active_stores = brands.filter((e) => e.status == 1);
      // arr to update the filteredItems
      let arr = [];
      // loop through stores to populate the filtered items 
      active_stores.forEach((store) => {
        let items = [];
        if(store.items?.length){
          let keyword_items = store.items.filter((x) => {
            let name        = x.name?.toUpperCase();
            let description = x.description?.toUpperCase();
            let key_word    = (keyword) ? keyword.toUpperCase() : '';
            return name.includes(key_word) || description.includes(key_word);
          })
          // sort by availability 
          // const keyword_items_available = keyword_items.sort((a,b) => b.available - a.available);
          // obj['items'] = keyword_items_available;
          // add store name and id in the items to display on the item level 
          // items = keyword_items;
          // console.log(items)
          const keyword_items_store = keyword_items.map(x => ({...x, store: {
            id: store.id, 
            name: store.name
          } }));
          if(keyword_items_store.length){
            arr.push(...keyword_items_store);
          }
        }
      })
      // sort based on availability 
      if(arr.length){
        arr = arr.sort((a,b) => b.available - a.available);
      }
      setFilteredItems(arr);
    }
  }

  // Set Item ID and go to store when clicking on the item
  const handleClickItem = (item) => {
    if(item.id && item.available && brands.length){
      // set the selectedBrand as well 
      const selected_brand = brands.find((x) => x.id == item.store?.id);
      if(selected_brand.id){
        // save to selectedBrand 
        handleSelectBrand(selected_brand);
        setItemId(item.id);
        history.push(`/customer/table-ordering/${table_id}/menu`);
      }
      // console.log(selected_brand);
      // console.log(item.id);
      // history.push(`/customer/table-ordering/`)
    }
  }
  

  useEffect(() => {
    handleSearchKeyword();
  }, [keyword])
  return (
    <CustomerLayout>
      {/* <NavbarCust
        goBack
        title={'Search'}
      /> */}
      <Container fluid>
        <div className="d-flex align-items-center ">
          <div className='d-flex mt-3 col-2 align-items-center text-secondary'>
            <Button
              className='rounded-pill border-0 text-secondary bg-transparent ps-0 pb-2'
              onClick={() => history.goBack()}
            >
              <BsChevronLeft size={20} style={{strokeWidth: '2px'}}/>
            </Button>
          </div>
          <div className="col-10">
            <Search
              focusOnInit
              searchFunction={setKeyword}
              placeholder={'Search for Foods or Store'}
            />
          </div>
        </div>
      </Container>
      {/* Show the results */}
      {
        keyword ? 
        <div className="bg-light mt-2" style={{minHeight: 'calc(100vh - 150px)'}}>
          <div className="my-3">
            <BrandList
              scrollable 
              goMenu
              keyword={keyword}
            />
          </div>
          {
            filteredItems.length ? 
              <>
                <Container fluid>
                  <h3 className="fw-bold">Items</h3>
                </Container>
                {filteredItems.map((item) => 
                  <Card onClick={e => handleClickItem(item)} className={cx(styles.card_menu, {
                    [styles.bg_unavailable] : ! item.available
                  })}>
                    <Card.Body>
                      <Row className="align-items-center">
                        <Col xs="3" md="4">
                          <img src={(item.image) ? item.image?.public_url : defaultImage} className={cx(styles.card_menu__image, {
                            [styles.card_menu__image__unavailable] : ! item.available
                          })}/>
                        </Col>
                        <Col xs="9" md="8">
                          <div className="fw-bold mb-0 font-tp">{item.name}</div>
                          <div className="small mb-2">{truncateText(item.description, 50)}</div>
                          <div >
                            <span className="d-inline-block small px-2 py-1 tp-rounded fw-bold shadow-sm me-1 text-primary" style={{ background: 'var(--tp-primary-4)'}}>{item.category?.name}</span>
                            <span className="d-inline-block small px-2 py-1 tp-rounded shadow-sm text-secondary" style={{background: 'var(--tp-secondary-5)'}}>
                              <FaStoreAlt className="me-1"/>
                              {item.store?.name}
                            </span>
                          </div>
                        </Col>
                      </Row>
                    </Card.Body>

                  </Card>                  
                )}
              </>
              :
              null
          }
          {!filteredItems.length && <NoData text={'Uh Oh, we can\t find what you\'re looking for.'}/>}
        </div>
        :
        <div className="d-flex align-items-center justify-content-center" style={{minHeight: 'calc(100vh - 250px'}}>
          <Card className="bg-light border-0">
            <Card.Body>
              <p className="mb-0">Search for your cravings!</p>
            </Card.Body>
          </Card>
        </div>
        }
    </CustomerLayout>
  )
}