/**
 * Search UI on the menu section
 * The search UI needs to accomodate for multi brands. so we need a dropdown here if
 * type == foodcourt
 **/
import { useEffect, useState } from 'react';
import { Row, Col, Form, Button } from 'react-bootstrap';
import {BiSearch} from 'react-icons/bi';
import { useHistory, useParams } from 'react-router';
export default function Search(props) {
  // pass the search key to the parent to handle the searches
  const {searchFunction, placeholder, globalSearch, focusOnInit} = props;
  const [searchKey, setSearchKey] = useState('');
  const [show, setShow] = useState(true);
  const history = useHistory();
  const {table_id} = useParams();
  const handleSubmit = (e) => {
    e.preventDefault();
    // if searchKey is empty, prevent execution
    if (!searchKey) {
      return false;
    }
  };
  const handleResetSearch = () => {
    setSearchKey('');
    if(searchFunction){
      searchFunction('');
    }
  }

  // if a globalSearch is present, we should lead it to the search page 
  const handleRedirectToGlobalSearch = () => {
    if(globalSearch){
      history.push(`/customer/search/${table_id}`);
    }
  }
  
  useEffect(() => {
    if(searchFunction){
      searchFunction(searchKey);
    }
  }, [searchKey]);

  useEffect(() => {
    if(focusOnInit){
      const search_form = document.getElementById('search-menu');
      search_form.focus();
    }
  }, [focusOnInit])

  return (
  <div>
    {
      show ? 
      <div>
        <Form className='mt-3 position-relative' onSubmit={handleSubmit}>
          <Form.Control
            // onBlur={e => setShow(false)}
            className='rounded-pill ps-5'
            id='search-menu'
            // onKeyUp={() =>{ searchFunction('search-menu')}}
            onChange={(e) => setSearchKey(e.target.value)}
            onClick={() => handleRedirectToGlobalSearch()}
            value={searchKey}
            placeholder={placeholder || 'Search For Food'}
            style={{
              minHeight: '42px',
            }}
          />
          {
            searchKey && 
            <span className='position-absolute px-2 bg-white-50 tp-rounded fw-bold'
            onClick={e => handleResetSearch()}
            style={{
              right:'1rem',
              top: '0.5rem',
              cursor: 'pointer'
            }}
            >x</span>
          }
          <i
            className='bi bi-search position-absolute'
            style={{
              left: '1rem',
              top: '0.5rem',
            }}
          />
        </Form>
      </div> : 
      <Button
        variant="light"
        className="rounded-circle"
        style={{padding: '0px'}}
        onFocus={e => setShow(true)}
      >
       <BiSearch size={20}/>
      </Button>
    }
  </div>
  )
}
