import React, {useEffect, useState} from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { useCustContext } from '../CustomerContext';
export default function BrandDiscountedMenu(props){
  const {brands} = useCustContext();

  const handleDiscountedItems = () => {
    if(brands.length){
      let discounted_menu = [];
      // get the menu that's on sale and find which store it is coming from 
      brands.forEach(i => {
        // const menu_items = i.items; 
        if(i.items?.length){
          let obj = {};
          let discounted_items = i.items.filter(x => parseInt(x.price_discount) > 0);
          if(discounted_items.length){
            obj['store_name'] = i.name;
            obj['items'] = discounted_items;
            discounted_menu.push(obj);
          }
        }
      });
      console.log(discounted_menu);
    }
  }

  useEffect(() => {
    handleDiscountedItems();
  }, [brands]);

  return null;

}