import { BsFolder2Open } from "react-icons/bs";

/**
 * A default reusable state for empty data page*/ 
export default function NoData(props){
  const {text} = props;
  return (
    <div className="text-center h5 d-flex align-items-center justify-content-center" >
      <div className="p-4">
        <BsFolder2Open size={55}/>
        <div className="mt-3">{text || 'No Data'}</div>
      </div>
    </div>
  )
}