import {React, useState, useEffect} from "react";
import { useHistory, useParams } from "react-router";
import { useCustContext } from "../CustomerContext";
import MenuBrand from "./MenuBrand";
import {Container, Row, Col} from 'react-bootstrap';
import cx from 'classnames';
import styles from '../assets/styles/MenuBrand.module.css';
import { AiOutlineLoading3Quarters } from "react-icons/ai";
import NoData from "./NoData";
export default function BrandList(props){
  const {brands, selectedBrand, handleSelectBrand, tableInfo, getBrands} = useCustContext();
  const {keyword} = props;
  const {table_id} = useParams();
  const history = useHistory();
  const [loading, setLoading] = useState(false);
  const [filteredBrands, setFilteredBrands] = useState([]);
  const clickSelectBrand = (item) => {
    handleSelectBrand(item);
    if(props.goBack){
      history.goBack();
    }
    if(props.goMenu){
      history.push(`/customer/table-ordering/${table_id}/menu`)
    }
  }

  const handleFilteredBrands = () => {
    const filter = (keyword) ? keyword.toUpperCase() : '';
    const filtered = brands.filter(x => {
      return x.name.toUpperCase().indexOf(filter) != -1 && x.status == 1
    });
    setFilteredBrands(filtered);
  }
  useEffect(() => {
    // check if brands empty. 
    if(! brands.length){
      setLoading(true);
      getBrands(table_id, false);
    }
    if(brands.length){
      setLoading(false);
    }
    if(filteredBrands.length && selectedBrand.id && props.scrollable){
      const scrollableDiv = document.getElementById('brand-scrollable-container');
      const selectedBrandDiv = document.getElementById(`brandCol-${selectedBrand.id}`);
      // scroll div to current selected brand 
      let offsetLeft = selectedBrandDiv.offsetLeft;
      let width = selectedBrandDiv.scrollLeft;
      scrollableDiv.scroll(offsetLeft - (width + 60) , 0);
    }
  }, [brands])

  useEffect(() => {
    handleFilteredBrands();
  }, [keyword, brands]);
  return (
    <>
    {
      loading && 
      <div className="text-center">
        <AiOutlineLoading3Quarters className="icon-spin text-primary" size={50}/>
      </div>
    }
    {/* {
      !filteredBrands.length && !loading && 
      <NoData
        text={'No Stores Found'}
      />
    } */}
    <Container fluid className={cx({
      'mt-3 px-0' : props.scrollable,
      'mt-2' : !props.scrollable
    })}>
      {
        filteredBrands.length ?
          <Container fluid>
            <h3 className="fw-bold">Stores</h3> 
          </Container>  : null
      }
      <div id={'brand-scrollable-container'}  className={cx({'container overflow-auto' : props.scrollable})}>
        <Row className={cx({'flex-nowrap' : props.scrollable})}>
        {filteredBrands.map((item, index) => {
          return (
            // only show the brands that are active
            item.status ? 
            <Col 
              xs="6" 
              md="4" 
              key={index} 
              onClick={() => clickSelectBrand(item)}
              className={cx({
                [styles.brandCol] : props.scrollable
              })}
              id={`brandCol-${item.id}`}
            >
              <MenuBrand
                category={item.category}
                logo={item.logo?.public_url}
                background={item.cover_photo?.public_url}
                text={item.name}
                selected={item.id === selectedBrand.id ? true : false}
              />
            </Col>
            : null
            );
        })}
        </Row>
      </div>
    </Container>
    </>
  )

}