/**
 * MAIN RESTAURANT MENU PAGE
 * url: /customer/menu/{restaurant}/{table}
 * This page contains all the menu details and item to add into their cart */
import React, { useEffect, useState, useRef } from 'react';
import CustomerLayout from '../layout/CustomerLayout';
import NavbarCust from '../components/NavbarCust';
import { Link, useParams, useHistory } from 'react-router-dom';
import { Container, Row, Col, Form, Button, Card } from 'react-bootstrap';
import Search from '../components/Search';
import MenuCategory from '../components/MenuCategory';
import MenuCardList from '../components/MenuCardList';
import FooterCart from '../components/MenuFooterCart';
import { getMenu, getOrderInfo } from '../api';
import BrandList from '../components/BrandList';
import { ItemDrawer } from '../components/ItemDrawer';
import { useCustContext } from '../CustomerContext';
import NoData from '../components/NoData';
export default function Menu() {
  const { selectedBrand, order, getBrands, scrollY } = useCustContext();
  // maybe we can get the table no from a context
  //const { restaurant, table } = useParams();
  const { table_id } = useParams();
  // the filtered menu items
  const [menuData, setMenuData] = useState([]);
  const [menuDataFilter, setMenuDataFilter] = useState([]);
  const [menuCategory, setMenuCategory] = useState([]);

  const handleFilterCategories = () => {
    let category_id = [];
    let categories = [];
    if (selectedBrand.items !== undefined) {
      selectedBrand.items.forEach((item) => {
        let check = category_id.includes(item.category.id);
        if (!check) {
          category_id.push(item.category.id);
          categories.push({ label: item.category.name, id: item.category.id });
        }
      });
    }
    setMenuCategory(categories);
  };
  useEffect(() => {
    handleFilterCategories();
    setMenuData(selectedBrand.items);
    setMenuDataFilter(selectedBrand.items);
    //setOrderData(orders);
  }, [selectedBrand]);
  const [searchStatus, setSearchStatus] = useState(false);
  const searchFunction = (value) => {
    // const input = document.getElementById(value);
    // const filter = input.value.toUpperCase();
    const filter = (value) ? value.toUpperCase() : '';
    if(filter){
      let data = [];
      menuData.forEach((item) => {
        if (
          item.name.toUpperCase().indexOf(filter) > -1 ||
          item.description.toUpperCase().indexOf(filter) > -1 || 
          item.category?.name.toUpperCase().indexOf(filter) > -1
        ) {
          data.push(item);
        }
      });
      if (data.length === 0) {
        setSearchStatus(true);
      } else {
        setSearchStatus(false);
        setMenuDataFilter(data);
      }
    }else{
      setMenuDataFilter(menuData);
      setSearchStatus(false);
    }

  };
  const [orderItems, setOrderItems] = useState({});
  const getOrder = async () => {
    try {
      if (order.id) {
        const res = await getOrderInfo(order.id);
        if(res?.data){
          const order = res.data.data.order;
          setOrderItems(order);
        }
      }
    } catch (err) {
      console.log(err);
    }
  };

  // if item drawer is close, fetch getOrder again
  const handleCloseItemDrawer = (i) => {
    getOrder();
  }


  useEffect(() => {
    if(menuDataFilter.length && scrollY){
      if(window){
        window.scroll(0,scrollY);
      }
    }
  }, [menuDataFilter])
  useEffect(() => {
    getOrder();
  }, [order]);
  useEffect(() => {
    getBrands(table_id, false);
  }, []);
  return (
    <CustomerLayout>
      <NavbarCust table={table_id} page={'home'} className={'bg-transparent'}/>
      <ItemDrawer
        handleCloseItemDrawer={handleCloseItemDrawer}
      />
      {/* 
        NOTE: the search and the category UI needs to be a sticky when scrolling. so we wrap them in a div 
      */}
      {/* <BrandList scrollable/> */}
      <div style={{background: `url(${selectedBrand.cover_photo?.public_url}) 50% 50%`, backgroundSize:'cover'}}>
        <div
          style={{height: '200px'}}
          className="d-flex justify-content-center align-items-center"
        >
        </div>
      </div>
      <div className='bg-white tp-rounded py-2' style={{marginTop: '-10px'}}>
        <Container>
          <img src={selectedBrand.logo?.public_url} className={'img-fluid shadow-sm mb-2 tp-rounded p-1'}  style={{height:'45px', objectFit:'contain'}}/>
          <h2 className='mb-0 fw-bold'>{selectedBrand.name}</h2>
        </Container>
      </div>
      <div className=' h-100'>
        <div>
          <div className='sticky-top sticky-md-none bg-light'>
            <Container fluid>
              <Row>
                <Col xs='12'>
                  {/* Search UI */}
                  <Search
                    //type={'food_center'}
                    searchFunction={searchFunction}
                  />
                </Col>
              </Row>
            </Container>
            {/*  Category Section */}
            <MenuCategory categories={menuCategory} />
          </div>
          {/* Menu Card section */}
          {!searchStatus && (
            <MenuCardList
              menu={menuDataFilter}
              categories={menuCategory}
              orders={orderItems}
              table={table_id}
            />
          )}
          {searchStatus && <div className='text-center'><NoData text={'We couldn\'t find any results'}/></div>}
          {/* Footer Cart */}
          {orderItems.order_items?.length ? <FooterCart orderItems={orderItems} /> : null}
        </div>
      </div>
    </CustomerLayout>
  );
}
