import React, { useEffect, useState } from 'react';
import NavbarCust from '../components/NavbarCust';
import { useCustContext } from '../CustomerContext';
import BrandList from '../components/BrandList';
import { Container, Image, Navbar } from 'react-bootstrap';
import Search from '../components/Search';
import BrandCategoryList from '../components/BrandCategoryList';
import BrandDiscountedMenu from '../components/BrandDiscountedMenu';
function Brands(props) {
  const { tableInfo, brands, user } = useCustContext();
  // const [search, setSearch] = useState('');
  // const searchFunction = (value) => {
  //   setSearch(value);
  // }

  // force scroll to top
  useEffect(() => {
    if (window) {
      window.scroll(0, 0);
    }
  }, []);
  return (
    <div className='position-relative bg-primary'>
      {/* <NavbarCust 
        page={'home'}
        className="bg-white"
      /> */}
      <Navbar expand='lg' className='bg-transparent pt-3'>
        <Container fluid>
          <div>
            <span
              className='px-3 fw-bold py-1 tp-rounded'
              style={{
                background: 'var(--tp-primary-4)',
                color: 'var(--tp-primary)',
              }}
            >
              {tableInfo.name}
            </span>
          </div>
        </Container>
      </Navbar>
      <div>
        <Container fluid className='py-4'>
          <p className='mb-0 text-white'>
            Hello there, <b className='fw-bold'>{user?.guess_name}</b>
          </p>
          <h4 className='fw-bold text-white'>
            Explore{' '}
            <span className='text-secondary'>{tableInfo.location?.name}</span>
          </h4>
          <Search placeholder='Search For Stores, Foods' globalSearch />
        </Container>
      </div>
      {brands && (
        <>
          <div
            className='tp-rounded'
            style={{
              minHeight: '100vh',
              background: 'var(--tp-gray-100)',
              borderBottomLeftRadius: '0px',
              borderBottomRightRadius: '0px',
            }}
          >
            <div className='pt-4'>
              <BrandList scrollable goMenu />
            </div>
            <Container fluid>
              <h3 className='fw-bold mt-1'>Discounted Items</h3>
              <BrandDiscountedMenu />
            </Container>
          </div>
        </>
      )}
    </div>
  );
}

export default Brands;
